/**
*  bottom subfooter section
*/
.c-subfooter-1 {
  // 18px = 1.125rem      1rem
  // 16px = 1rem or 1em   0.8889ren
  // 13px = 0.8125rem     0.7222rem
  margin: 0;
  padding: 0;
  border: 0;
  background: $dark;
  // font-size: .8125rem;
  font-size: 0.7222rem;
  // pending  - where is this colour coming from
  // color: $white;
  font-weight: normal;
  line-height: 1.125rem;

  a {
    color: $lesslight;
    font-weight: normal;
  }

  &__wrap {
    display: grid;
    width: 100%;
    max-width: 1200px;
    margin-right: auto;
    margin-left: auto;

    /* small display  */
    @include forLargeScreens(bp1) {
      grid-template-areas:
        'footer-nav'
        'project-by'
        'follow-us'
        'stats'
        'copyright'
        'pg-ad';
      grid-template-columns: 1fr;
    }

    /* medium display */
    @include forLargeScreens(bp2) {
      grid-template-areas:
        'footer-nav project-by'
        'footer-nav follow-us'
        'stats copyright'
        'pg-ad pg-ad';
      grid-template-columns: 1fr 1fr;
    }

    /* wide display */
    @include forLargeScreens(bp3) {
      grid-template-areas:
        'footer-nav project-by stats'
        'footer-nav follow-us  stats'
        'copyright copyright copyright'
        'pg-ad pg-ad pg-ad';
      grid-template-columns: 36% 28% 36%;
      // align-items: start;
    }
  }
}


/* subfooter nav */
.c-subfooter-1__nav {
  grid-area: footer-nav;
  margin: 1rem 1.75rem 0;
  border: 0.125rem solid $lesslight;
  // background: $debug-green;

  &-list {
    margin-bottom: 0;
    margin-left: 0;
  }

  &-heading {
    margin-bottom: 0;
    padding: 0.25rem 0 0.15rem;
    text-align: center;
  }

  &-item {
    width: auto;
    padding: 0 1.625rem 0.15rem;
    list-style-type: none;
  }

  &-item:nth-child(3) {
    padding-bottom: 1.425rem;
  }

  // &-item:last-child {
  //     padding: 0 1.625rem .25rem;
  // }
}


/* fug project section */
.c-subfooter-1__fug {
  grid-area: project-by;
  padding: 0 0.5rem;
  text-align: center;
  // background: $debug-limegreen;

  &-heading {
    margin: 1rem 0 0.25rem;
  }

  &-nav.c-subfooter-1__fug-nav.c-subfooter-1__fug-nav {
    margin-top: 0;
  }
}


/* follow us section */
.c-subfooter-1__social-nav {
  grid-area: follow-us;
  padding: 0 0.5rem;
  list-style-type: none;
  text-align: center;
  // background: $debug-purple;

  &-heading {
    margin: 0.25rem;
  }

  &-list {
    margin: 0;
    list-style-type: none;
  }
}


/* statistics section */
.c-subfooter-1__stats {
  grid-area: stats;
  margin: 1rem 1.75rem 0;
  border: 0.125rem solid $lesslight;
  // background: $debug-darklime;

  &-heading {
    margin-bottom: 0;
    padding: 0.25rem 0 0.15rem;
    text-align: center;
  }

  &-list {
    margin-bottom: 0;
  }

  &-term {
    padding: 0 1.625rem 0.15rem;
  }

  &-value {
    padding: 0 0.5rem 0.15rem 0.25rem;
  }

}


/* copyright section */
.c-subfooter-1__copyright {
  grid-area: copyright;
  padding: 0 2rem;
  text-align: center;
  // background: $debug-blue;

  &-text {
    margin-top: 0.75rem;
    margin-bottom: 0.25rem;
  }
}


/* ads space */
.c-subfooter-1__ads-space {
  grid-area: pg-ad;
  height: 5.625rem;
}
